<template>
  <div class="stream-status" v-if="streamStatus !== null">
    <p>Stream is <a href="https://www.twitch.tv/lowco">ONLINE</a></p>
  </div>
</template>

<script>
import TwitchGateway from '../../gateways/twitch.js'
export default {
  name: 'stream-status',
  data () {
    return {
      streamStatus: null
    }
  },
  mounted () {
    TwitchGateway.isStreamLive(52925091).then(function (results) {
      this.streamStatus = results.body.stream
    }.bind(this))
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
a{
  display: inline-block;
  border: 2px solid $light-purple;
  border-radius: 25px;
  padding: 5px 20px;
  margin-left: 5px;
  color: $font-white;
  transition: $standard-transition;
  text-decoration: none;
  background-color: $light-purple;
  &:hover{
    transform: scale(1.1);
  }
}
</style>



