<template>
  <div>
    <a href="https://twitter.com/CatalystTwitch" title="twitter" target="_blank">
      <icon name="twitter" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://discord.gg/XXEVjXu" title="discord" target="_blank">
      <icon name="discord" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank">
      <icon name="youtube" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank">
      <icon name="instagram" class="icon" :scale="iconSize"></icon>
    </a>
    <a href="https://www.twitch.tv/team/catalyst" title="twitch" target="_blank">
      <icon name="twitch" class="icon" :scale="iconSize"></icon>
    </a>
  </div>
</template>

<script>
export default {
  name: 'social-media-icons',
  data () {
    return {
      iconSize: 1.5
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.icon{
  fill: $font-white;
  vertical-align: bottom;
  margin: 0 10px;
  transition: $standard-transition;
  &:hover{
    fill: $light-purple;
    transform: scale(1.1);
  }
}
</style>



