<template>
  <footer class="footer">
    <span>LOWCO.TV &copy; 2017</span>
    <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
  </footer>
</template>

<script>
import SocialMediaIcons from './header/SocialMediaIcons'

export default {
  name: 'footer',
  components: {SocialMediaIcons}
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.footer{
  background-color: $dark-purple;
  color: $font-white;
  padding: 15px 30px;
  text-align: left;
  span{
    display: inline-block;
  }
  .social{
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px;
  }
}
</style>



