<template>
  <div class="blog-post">
  	<h3>{{title}}</h3>
  	<span>{{age}}</span>
  	<div v-html="styled" class="blog-clear" v-bind:class="{ expanded: isExpanded }"></div>
    <button v-on:click="expand">Read More</button>
  </div>
</template>

<script>
export default {
  name: 'blogpost',
  props: ['title', 'age', 'description'],
  data () {
    return {
      isExpanded: false
    }
  },
  methods: {
    expand: function () {
      console.log(this.isExpanded)
      this.isExpanded = !this.isExpanded
      console.log(this.isExpanded)
    }
  },
  computed: {
    styled: function () {
      var reg = /background-color.*?;|font-family.*?;/g
      return this.description.replace(reg, '')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-post{
	background-color: $font-white;
  max-width: 700px;
  margin: 0 auto;
  border-radius: 15px;
  padding: 25px;
  transition: $standard-transition;
  margin: 25px auto;
  padding: 25px;
}
.blog-clear{
  text-align: initial;
  max-height: 350px;
  overflow: hidden;
  transition: $blog-transition;
}
.expanded{
  max-height: 2000px;
}
</style>



