import Vue from 'vue'

var twitchHeader = {headers: {'Client-ID': 'oj79pe90wcf8nan97jzuy6dm2h7fh0', 'Accept': 'application/vnd.twitchtv.v5+json'}}

export default {
  isStreamLive (channelId) {
    var url = 'https://api.twitch.tv/kraken/streams/' + channelId
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url, twitchHeader).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  },
  getChannelInfo (channelId) {
    var url = 'https://api.twitch.tv/kraken/channels/' + channelId
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url, twitchHeader).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



