import Vue from 'vue'

var channelId = 'UCIE_BvU71U28q6It6F6dl0g'
var key = 'AIzaSyDBzj0pxOrn64NBkWOVKI0l6c5afobDfP0'
var maxResults = 5
var part = 'contentDetails'

export default {
  getPlaylists () {
    var url = 'https://www.googleapis.com/youtube/v3/playlists?channelId=' + channelId + '&key=' + key + '&maxResults=' + maxResults + '&part=' + part
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



