
import Vue from 'vue'
var apiKey = 'AIzaSyBCZKze-EwYoM0NnZRCd2bxGmhgCibH18c'
var blogId = '8716246814798641325'

export default {
  getBlogPosts (channelId) {
    var url = 'https://www.googleapis.com/blogger/v3/blogs/' + blogId + '/posts?key=' + apiKey
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



