<template>
  <main class="home">
    <About></About>
    <Twitch></Twitch>
    <Social></Social>
    <Youtube></Youtube>
    <Newsletter></Newsletter>
  </main>
</template>

<script>
import About from './home/About'
import Twitch from './home/Twitch'
import Social from './home/Social'
import Youtube from './home/Youtube'
import Newsletter from './home/Newsletter'

export default {
  name: 'home',
  components: {
    About,
    Twitch,
    Social,
    Youtube,
    Newsletter
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



