<template>
  <div class="youtube">
    <h2><a class="link-btn" href="https://www.youtube.com/user/Lowco2525">My Youtube</a></h2>
    <div class="video-wrapper">
      <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[0].id)" frameborder="0" allowfullscreen></iframe></div>
      <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[1].id)" frameborder="0" allowfullscreen></iframe></div>
      <div class="iframe-wrap"><iframe :src="getPlaylistString(youtubeList[2].id)" frameborder="0" allowfullscreen></iframe></div>
    </div>
  </div>
</template>

<script>
import YoutubeGateway from '../../gateways/youtube.js'
export default {
  name: 'youtube',
  data () {
    return {
      youtubeList: [{id: ''}, {id: ''}, {id: ''}]
    }
  },
  methods: {
    getPlaylistString (targetId) {
      return targetId === '' ? '' : 'https://www.youtube.com/embed/videoseries?list=' + targetId
    }
  },
  created () {
    YoutubeGateway.getPlaylists().then(function (results) {
      console.log(results)
      this.youtubeList = results.body.items
    }.bind(this))
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.youtube{
  padding: $home-section-padding;
  background-color: $dark-purple;
  color: $font-gray;
  text-align: left;
  h2{
    margin-top: 6px;
  }
}

.video-wrapper {
  position: relative;
  justify-content: space-between;
  display: flex;
  width: 100%;
  text-align: center;
}

.iframe-wrap {
  position: relative;
  padding-right: 25px;
  width: 100%;
}

.iframe-wrap:before{
  content: "";
  display: inline-block;
  padding-top: 56.25%; /* 16:9 */
}

.iframe-wrap iframe {
  margin: 0 auto;
  width: 100%;
  height: 98%;
}
</style>



