<template>
  <header class="nav">
    <div class="nav-top">
      <router-link to="/" title="Return to home"><img class='logo' src="../assets/graphics/logo.png" alt="lowco-logo"/></router-link>
      <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
    </div>
    <div class="nav-mid">
      <video ref="" :src="videoRef" id="video-container" width="100%" autoplay="autoplay" loop="loop"/>
      <div class="centered">
        <img class="logo-sm" src="../assets/graphics/logo-70x70.png" alt="lowco-logo"/>
        <h1>fulltime variety caster</h1>
        <a class="link-btn" href="https://www.twitch.tv/lowco">Live Stream</a>
      </div>
    </div>
    <nav class="nav-bar">
      <router-link to="/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>home</div></router-link><!--
      --><router-link to="/calendar"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>calendar</div></router-link><!--
      --><router-link to="/blog"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>blog</div></router-link><!--
      --><router-link to="/contact"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>contact</div></router-link><!--
      --><a href="https://shop.spreadshirt.com/lowco2525/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>shop</div></a><!--
      --><a href="https://www.twitch.tv/lowco/"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>steam</div></a>
    </nav>
  </header>
</template>

<script>
import SocialMediaIcons from './header/SocialMediaIcons'

export default {
  name: 'header-bar',
  components: {SocialMediaIcons},
  computed: {
    videoRef () { return 'http://' + window.location.host + '/static/videoheader.mp4' }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
#video-container{
  margin-bottom: -4px;
}
.nav{
  text-transform: uppercase;
  background-color: $dark-purple;
  padding-top: 20px;
  border-bottom: 3px solid $light-purple;
  h1{
    color: $font-white;
    font-weight: 100;
    margin-top: 0;
  }
  a{
    text-decoration: none;
  }
  .logo{
    width: 200px;
    vertical-align: bottom;
  }
  &-top{
    display: flex;
    justify-content: space-between;
    vertical-align: bottom;
    width: 90%;
    max-width: 1500px;
    margin: 0 auto;
    .social{
      display: inline-block;
      margin: 15px 0;
    }
  }
  &-mid{
    position: relative;
    .centered{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .logo-sm{
      border-radius: 70px;
    }
  }
  &-bar{
    .icon{
      display: block;
      visibility: hidden;
      margin: 0 auto;
      fill: $font-white;
    }
    a{
      display: inline-flex;
      position: relative;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 30px 0;
      color: $font-white;
      width: 100px;
      transition: $standard-transition;
      margin-right: 1px;
      .nav-link-wrap{
        padding-bottom: 20px;
      }
      &~a {
        .nav-link-wrap:before {
          content:"";
          position: absolute;
          left: -1px;
          background: #1e1e1e;
          height: 50px;
          width: 1px;
        }
      }
      &:hover,
      &:focus{
        background-color: $light-purple;
      }
    }
    .router-link-exact-active{
      background-color: $light-purple;
      a{
        color: $font-white;
      }
      .icon{
        visibility: visible;
        fill: $font-white;
      }
    }
  }
}
</style>



