<template>
  <div class="twitch">
    <div class='twitch-wrapper'>
      <div class='twitch-stream'>
        <iframe src="https://player.twitch.tv/?channel=lowco&muted=true" frameborder="0" scrolling="no"></iframe>
      </div>
    </div>
    <div class="twitch-description">
      <h2><a class="link-btn" href="https://www.twitch.tv/lowco">My Twitch</a></h2>
      <div class="twitch-description-text">
        <p>I mostly plays popular/new games for PC, PS4, and XB1.</p>
        <p>My schedule: Monday to Saturday 11am to 5pm EST</p>
      </div>
      <stream-status></stream-status>
    </div>
  </div>
</template>

<script>
import StreamStatus from '../global/StreamStatus'

export default {
  name: 'twitch',
  components: {StreamStatus}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.twitch{
  display: flex;
  justify-content: center;
  padding: $home-section-padding;
  background-color: $dark-purple;
  color: $font-white;
}

.twitch-wrapper {
  position: relative;
  display: inline-block;
  width: 50%;
}

.twitch-description {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  margin-left: 65px;
}

.twitch-stream {
  position: relative;
  width:100%;
  border-radius: 10px;
  overflow: hidden;
}

.twitch-stream:before{
  content: "";
  display: block;
  padding-top: 56.25%; /* 16:9 */
}

.twitch-stream iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>



