<template>
  <div class="social">
    <h2 class="section-header">Social Media</h2>
    <p>Check my social media links, follow me to stay updated</p>
    <a href="https://twitter.com/CatalystTwitch" title="twitter" target="_blank">
      <div class="link-wrap">
          <icon name="twitter" class="icon" :scale="iconSize"></icon>
          <label>Twitter</label>
      </div>
    </a>
    <a href="https://discord.gg/XXEVjXu" title="discord" target="_blank">
      <div class="link-wrap">
          <icon name="discord" class="icon" :scale="iconSize"></icon>
          <label>Discord</label>
      </div>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank">
      <div class="link-wrap">
          <icon name="youtube" class="icon" :scale="iconSize"></icon>
          <label>Youtube</label>
      </div>
    </a>
    <a href="https://www.youtube.com/user/Lowco2525" title="youtube" target="_blank">
      <div class="link-wrap">
          <icon name="instagram" class="icon" :scale="iconSize"></icon>
          <label>Instagram</label>
      </div>
    </a>
    <a href="https://www.twitch.tv/team/catalyst" title="twitch" target="_blank">
      <div class="link-wrap">
          <icon name="twitch" class="icon" :scale="iconSize"></icon>
          <label>Twitch</label>
      </div>
    </a>
  </div>
</template>

<script>
import SocialMediaIcons from '../header/SocialMediaIcons'
export default {
  name: 'twitch',
  components: {SocialMediaIcons},
  data () {
    return {
      iconSize: 3
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
  .social{
    padding: $home-section-padding;
  }
  .icon{
    display: block;
    fill: $light-purple;
    transition: $standard-transition;
    margin: 0 auto;
  }
  .link-wrap{
    display: inline-block;
    padding: 10px;
    text-align: center;
    border-radius: 20px;
    margin: 15px;
    transition: $standard-transition;
    label{
      color: $font-gray;
      transition: $standard-transition;
    }
    &:hover{
      background-color: $light-purple;
      transform: scale(1.1);
      label{
        color: $font-white;
      }
      .icon{
        fill: $font-white;
      }
    }
  }
</style>



