<template>
  <div class="blog-list">
    <blog-post :title="blog.title" :age="blog.updated" :description="blog.content" v-for="blog in blogs"></blog-post>
  </div>
</template>

<script>
import BlogPost from './BlogPost'
import api from '../../gateways/blogger'
export default {
  name: 'bloglist',
  components: {BlogPost},
  data () {
    return {
      blogs: {}
    }
  },
  created: function () {
    api.getBlogPosts().then(function (results) {
      console.log(results.body)
      this.blogs = results.body.items
    }.bind(this))
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-list{
  background-color: $dark-purple;
  padding: $home-section-padding;
}
</style>



